import React from "react";
import styled from "styled-components";

import FlexBox from "../FlexBox";

const StyledHeaderLayout = styled(FlexBox)`
  color: tomato;
  border-color: tomato;
`;

export default function HeaderLayout(): JSX.Element {
  return (
    <>
      <FlexBox />
      <StyledHeaderLayout />
    </>
  );
}
